import React from "react";
import Box from "@mui/material/Box";
import { DataGrid } from "@mui/x-data-grid";
import moment from "moment";
import "moment/locale/ru";
import CustomNoRowsOverlay from "./norows";

function getStatus(pipeline, statusId) {
  const currentStatus = pipeline.find((status) => status.id === statusId);
  return currentStatus.name;
}

function getResponsibleUser(usersList, userId) {
  const currentUser = usersList.find((user) => user.id === userId);
  return currentUser.name;
}

function getData(time) {
  moment.locale("ru");
  const date = moment.unix(time).format("LL");
  return date;
}

export default function TableSection({
  leadsList,
  pipeline,
  usersList,
  isLoading,
}) {
  const leadsStatuses = pipeline._embedded.statuses;

  const leadsTableSetting = [
    {
      field: "name",
      headerName: "",
      width: 250,
      editable: true,
    },
    {
      field: "status_id",
      headerName: " ",
      width: 160,
      valueGetter: (params) => getStatus(leadsStatuses, params.value),
    },
    {
      field: "responsible_user_id",
      headerName: "",
      width: 180,
      valueGetter: (params) => getResponsibleUser(usersList, params.value),
    },
    {
      field: "created_at",
      headerName: " ",
      width: 180,
      valueGetter: (params) => getData(params.value),
    },
    {
      field: "price",
      headerName: "",
      type: "number",
      width: 110,
    },
  ];

  return (
    <Box sx={{ mt: 4, flexGrow: 1, width: "100%" }}>
      <DataGrid
        components={{
          NoRowsOverlay: CustomNoRowsOverlay,
        }}
        loading={isLoading}
        autoHeight
        rows={leadsList}
        columns={leadsTableSetting}
        pageSize={10}
        rowsPerPageOptions={[10]}
        disableSelectionOnClick
        experimentalFeatures={{ newEditingApi: true }}
      />
    </Box>
  );
}
