import React, { useState, useEffect } from "react";
import AppBar from "./components/AppBar";
import { Container } from "@mui/material";
import api from "./utils/api";
import TableSection from "./components/TableSection";

function App() {
  const [leadsList, setLeadsList] = useState([]);
  const [pipeline, setPipeline] = useState([]);
  const [usersList, setUsersList] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [leadsLoading, setLeadsLoading] = useState(true);

  useEffect(() => {
    api
      .getAllLeads()
      .then(([leads, pipelines, users]) => {
        const [currentPipeline] = pipelines._embedded.pipelines;
        setLeadsList(leads._embedded.leads);
        setPipeline(currentPipeline);
        setUsersList(users._embedded.users);
      })
      .catch((err) => console.log(err))
      .finally(() => {
        setLeadsLoading(false);
        setIsLoading(false);
      });
  }, []);

  function handleSearch(searchValue) {
    if (searchValue.length >= 3 || searchValue.length === 0) {
      setLeadsLoading(true);
      api
        .getSearchLeads(searchValue)
        .then(([leads, pipelines, users]) => {
          const [currentPipeline] = pipelines._embedded.pipelines;
          setLeadsList(leads ? leads._embedded.leads : []);
          setPipeline(currentPipeline);
          setUsersList(users._embedded.users);
        })
        .catch((err) => console.log(err))
        .finally(() => {
          setLeadsLoading(false);
        });
    }
  }

  return isLoading ? (
    <p>Loading</p>
  ) : (
    <>
      <AppBar onSearch={handleSearch} />
      <Container>
        <TableSection
          leadsList={leadsList}
          pipeline={pipeline}
          usersList={usersList}
          isLoading={leadsLoading}
        />
      </Container>
    </>
  );
}

export default App;
